  .underlineHover:after {
    display: block;
    left: 0;
    bottom: -10px;
    width: 0;
    height: 2px;
    background-color: #009b91;
    content: "";
    transition: width 0.2s;
  }
  
  .underlineHover:hover {
    color: #0d0d0d;
  }
  
  .underlineHover:hover:after {
    width: 100%;
  }
  
  
  *:focus {
    outline: none;
  }
  
  * {
    box-sizing: border-box;
  }
  
  .icon_user {
    width: 40px;
  
